<template>
  <div class="main-parent">
    <div class="regsiter-form">
      <h1 class="txt-center welcome-caption">Welcome To Cric11</h1>
      <h2 class="txt-center form-title">Login</h2>
      <p class="caption-txt txt-center">Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
      <v-form>
        <v-text-field v-model="email" :rules="emailRules" label="E-mail" required prepend-icon="email"
          class="form-field"></v-text-field>
        <v-text-field v-model="password" :type="errorMsg ? 'text' : 'password'" name="input-10-1" label="Password"
          hint="At least 8 characters" counter @click:append="errorMsg = !errorMsg" prepend-icon="lock"
          class="form-field"></v-text-field>
        <v-btn block color="primary" dark large class="reg-btn" @click="handleLogin">Login</v-btn>
      </v-form>
      <p class="link">
        Don't have an account?
        <router-link class="signin" to="/register">Sign Up</router-link>
      </p>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+/.test(v) || "E-mail must be valid"
      ],
      password: "",
      number: "",
      errorMsg: false
    };
  },
  methods: {
    handleLogin() {
      let reqUrl =
        "/cric11/auth/login";
      let headers = {
        email_address: this.email,
        password_data: this.password
      };

      axios
        .get(reqUrl, { headers })
        .then(response => {
          // JSON responses are automatically parsed.
          console.log(response);
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.main-parent {
  background-image: url("../assets/bg.jpg");
  height: 100vh;
  display: flex;
  align-items: center;
}
.regsiter-form {
  background-color: #fff;
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}
.welcome-caption {
  padding-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
  color: #1867c0;
}
.form-title {
  margin: 10px 0;
  color: #333;
}
.txt-center {
  text-align: center;
}
.form-field {
  padding: 10px 0;
}
.caption-txt {
  padding: 0 30px;
  color: #777;
  font-size: 16px;
  margin-bottom: 30px;
}
.reg-btn {
  margin-top: 20px;
  font-weight: bold;
}
.link {
  padding-top: 30px;
  text-align: center;
  font-size: 16px;
  color: #555;
}
.signin {
  font-weight: bold;
  color: #1867c0;
}
</style>



